import React, { FC, memo } from 'react';
import { TextFieldStandard } from '../../MUiKit/TextFields/TextFields';

interface IEmailFieldProps {
  userRegisterEmail: string;
  setUserRegisterEmail: (e: string) => void;
}

export const EmailField: FC<IEmailFieldProps> = memo(
  ({ userRegisterEmail, setUserRegisterEmail }) => {
    return (
      <>
        <TextFieldStandard
          value={userRegisterEmail}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            setUserRegisterEmail(e.target.value)
          }
          variant="standard"
          margin="normal"
          required
          id="email"
          label="Email"
          name="email"
          autoComplete="off"
          error={userRegisterEmail.length > 6 && userRegisterEmail.indexOf('@') === -1}
        />
      </>
    );
  },
);
