import React, { FC, memo } from 'react';
import { TextFieldStandard } from '../../MUiKit/TextFields/TextFields';

interface INameFieldProps {
  userRegisterName: string;
  setUserRegisterName: (e: string) => void;
}

export const NameField: FC<INameFieldProps> = memo(({ userRegisterName, setUserRegisterName }) => {
  return (
    <>
      <TextFieldStandard
        value={userRegisterName}
        onChange={(e: React.ChangeEvent<HTMLInputElement>) => setUserRegisterName(e.target.value)}
        variant="standard"
        margin="normal"
        required
        id="name"
        label=", "
        name="name"
        autoComplete="off"
      />
    </>
  );
});
