import React, { FC, memo } from 'react';
import { TextFieldStandard } from '../../MUiKit/TextFields/TextFields';

interface IPhoneFieldProps {
  userRegisterPhone: string;
  setUserRegisterPhone: (e: string) => void;
}

export const PhoneField: FC<IPhoneFieldProps> = memo(
  ({ userRegisterPhone, setUserRegisterPhone }) => {
    return (
      <>
        <TextFieldStandard
          value={userRegisterPhone}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            setUserRegisterPhone(e.target.value)
          }
          variant="standard"
          margin="normal"
          required
          id="phone"
          error={userRegisterPhone.length > 12}
          label=" "
          name="phone"
          autoComplete="off"
        />
      </>
    );
  },
);
