import { Link } from 'react-router-dom';
import { NameField } from './Fields/NameField';
import { PhoneField } from './Fields/PhoneField';
import { EmailField } from './Fields/EmailField';
import { PasswordField } from './Fields/PasswordField';
import { ConfirmPasswordField } from './Fields/ConfirmPasswordField';
import React, { Dispatch, FC, SetStateAction, useEffect, useState } from 'react';
import { BackHome } from '../BackHome/BackHome';
import { ButtonGreen } from '../MUiKit/Buttons/Buttons';
import { useDispatch } from 'react-redux';
import { getUserRegisterData } from '../../store/reducers/RegisterUserSlice/slice';

interface IRegistrationForm {
  setClick: Dispatch<SetStateAction<boolean>>;
}

export const RegistrationForm: FC<IRegistrationForm> = ({ setClick }) => {
  const [userRegisterName, setUserRegisterName] = useState('');
  const [userRegisterPhone, setUserRegisterPhone] = useState('+7');
  const [userRegisterEmail, setUserRegisterEmail] = useState('');
  const [userRegisterPass, setUserRegisterPass] = useState('');
  const [userRegisterConfirmPass, setUserRegisterConfirmPass] = useState('');
  const [enableRegisterButton, setEnableRegisterButton] = useState(false);
  const dispatch = useDispatch();

  const handleClickRegisterButton = () => {
    setClick(true);
    dispatch(
      getUserRegisterData({
        login: userRegisterPhone,
        phone_number: true,
        name: userRegisterName,
        password: userRegisterPass,
      }),
    );
  };

  useEffect(() => {
    if (
      userRegisterPass === userRegisterConfirmPass &&
      userRegisterName.length > 0 &&
      userRegisterPhone.length > 0 &&
      userRegisterPass.length > 0
    ) {
      setEnableRegisterButton(true);
    } else setEnableRegisterButton(false);
  }, [userRegisterPass, userRegisterName, userRegisterPhone, userRegisterConfirmPass]);

  return (
    <div className={'registration-form__container'}>
      <BackHome title={''} link={'/'} />
      <div className={'registration-form__form'}>
        <div>
          <NameField
            userRegisterName={userRegisterName}
            setUserRegisterName={setUserRegisterName}
          />
          <PhoneField
            userRegisterPhone={userRegisterPhone}
            setUserRegisterPhone={setUserRegisterPhone}
          />
          <EmailField
            userRegisterEmail={userRegisterEmail}
            setUserRegisterEmail={setUserRegisterEmail}
          />
          <PasswordField
            userRegisterPass={userRegisterPass}
            setUserRegisterPass={setUserRegisterPass}
          />
          <ConfirmPasswordField
            userRegisterConfirmPass={userRegisterConfirmPass}
            setUserRegisterConfirmPass={setUserRegisterConfirmPass}
          />
        </div>
      </div>
      <div className={'registration-form__bottom'}>
        <div>
          <ButtonGreen onClick={handleClickRegisterButton} disabled={!enableRegisterButton}>
            
          </ButtonGreen>
        </div>
        <span className={'registration-form__bottom-redirect'}>
               {' '}
          <Link to={'/login'}>
            <b></b>
          </Link>
        </span>
      </div>
    </div>
  );
};
