import React, { FC, memo } from 'react';
import { TextFieldStandard } from '../../MUiKit/TextFields/TextFields';

interface IConfirmPasswordFieldProps {
  userRegisterConfirmPass: string;
  setUserRegisterConfirmPass: (e: string) => void;
}

export const ConfirmPasswordField: FC<IConfirmPasswordFieldProps> = memo(
  ({ userRegisterConfirmPass, setUserRegisterConfirmPass }) => {
    return (
      <>
        <TextFieldStandard
          value={userRegisterConfirmPass}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            setUserRegisterConfirmPass(e.target.value)
          }
          variant="standard"
          margin="normal"
          required
          name="current-password"
          label="  "
          type="password"
          id="current-password"
          autoComplete="off"
        />
      </>
    );
  },
);
