import React, { FC, memo } from 'react';
import { TextFieldStandard } from '../../MUiKit/TextFields/TextFields';

interface IPasswordFieldProps {
  userRegisterPass: string;
  setUserRegisterPass: (e: string) => void;
}

export const PasswordField: FC<IPasswordFieldProps> = memo(
  ({ userRegisterPass, setUserRegisterPass }) => {
    return (
      <>
        <TextFieldStandard
          value={userRegisterPass}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => setUserRegisterPass(e.target.value)}
          variant="standard"
          margin="normal"
          required
          name="password"
          label="  "
          type="password"
          id="password"
          autoComplete="off"
        />
      </>
    );
  },
);
