import React, { Dispatch, FC, SetStateAction, useEffect, useState } from 'react';
import { BackHome } from '../BackHome/BackHome';
import { OtpForm } from './OtpForm/OtpForm';
import { ButtonGreen } from '../MUiKit/Buttons/Buttons';

interface IConfirmRegistrationForm {
  setClick: Dispatch<SetStateAction<boolean>>;
}

export const ConfirmRegistrationForm: FC<IConfirmRegistrationForm> = ({ setClick }) => {
  const [timerOtp, setTimerOtp] = useState(10);
  const [otpData, setOtpData] = useState('');

  useEffect(() => {
    const timer = setInterval(() => {
      setTimerOtp(timerOtp - 1);
    }, 1000);
    if (timerOtp === 0) clearInterval(timer);
    return () => clearInterval(timer);
  }, [timerOtp]);

  const min = Math.floor(timerOtp / 60);
  const sec = timerOtp % 60;

  const handleSendNewOtp = () => {
    setTimerOtp(10);
  };

  return (
    <div className={'confirm-registration__container'}>
      <BackHome title={''} link={'/signup'} setClick={setClick} />
      <div className={'confirm-registration__form'}>
        <h2></h2>
        <h3>    C  </h3>
        <div className={'confirm-registration__form-otp'}>
          {timerOtp > 0 ? (
            <span>
              {min}:{sec < 10 ? `0${sec}` : sec}
            </span>
          ) : (
            <span onClick={handleSendNewOtp}> </span>
          )}
          <OtpForm otpData={otpData} setOtpData={setOtpData} />
        </div>
      </div>

      <div className={'confirm-registration__bottom'}>
        <div>
          <ButtonGreen disabled={otpData.length !== 6} onClick={() => alert('')}>
            
          </ButtonGreen>
        </div>
      </div>
    </div>
  );
};
