import React, { FC } from 'react';
import { MuiOtpInput } from 'mui-one-time-password-input';

interface IOtpFormProps {
  otpData: string;
  setOtpData: (newValue: string) => void;
}

export const OtpForm: FC<IOtpFormProps> = ({ otpData, setOtpData }) => {
  const widthScreen = window.innerWidth;

  const handleChangeOtp = (newValue: string) => {
    setOtpData(newValue);
  };

  return (
    <MuiOtpInput
      value={otpData}
      onChange={handleChangeOtp}
      length={6}
      sx={{ width: widthScreen > 480 ? 343 : '90vw' }}
      gap={1}
      autoFocus={true}
    />
  );
};
