import { styled, TextField } from '@mui/material';

const widthScreen = window.innerWidth;

export const TextFieldStandard = styled(TextField)(({ theme }) => ({
  fontSize: 14,
  fontWeight: 300,
  width: widthScreen > 480 ? 343 : '90vw',
  display: 'flex',
  justifyContent: 'center',
  '& label.Mui-focused': {
    color: '#3AA17B',
  },
  ' & .MuiInput-underline::after ': {
    borderColor: '#3AA17B',
  },
}));
